* {
    box-sizing: border-box;
  }
  
  .container {
    width: 100%;
    margin: 0 auto;
  }
  
  .container:after,
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  .row {
    margin-left: -15px;
    margin-right: -15px;
  }
  
  [class*='col-'] {
    float: left;
    padding: 0 1em;
    width: 100%;
    min-height: 1px;
    display: inline-block;
  }
  @media #{$breakpoint-xs} {
    .container {
      width: 95%;
      max-width: $grid-max-width;
    }
    @for $i from 1 through $grid-columns {
      .col-xs-#{$i} {
        width: 100% / $grid-columns * $i;
      }
  
      .col-xs-offset-#{$i} {
        margin-left: 100% / $grid-columns * $i;
      }
    }
  }
  @media #{$breakpoint-small} {
